$color-1 : rgba(0, 0, 0, 0.5);
$color-2 : rgb(249,134,7);


.all-wrapper {
  position: absolute;
  right: 150px;
  top: 150px;
  .prop-util-wrapper {
    width: 400px;
    height: 590px;
    background-color: $color-1;
    border: 2px solid $color-2;
    header {
      height: 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .title-wrapper {
        display: flex;
        .title {
          font-size: 20px;
          padding-left: 10px;
          background-color: $color-2;
        }
        .top-left-corner {
          height: 30px;
          width: 30px;
          background: linear-gradient(135deg, $color-2 50%, rgba(0,0,0,0) 50%);
        }
      }
      .exit {
        height: 30px;
        width: 30px;
        padding-right: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        .svg {
          height: 30px;
          width: 30px;
          svg {
            width: 30px;
            height: 30px;
            color: aliceblue;
            &:hover {
              color: $color-2;
            }
          }
        }
      }
    }
    main {
      .prop-list-wrapper{
        padding: 5px;
        box-sizing: border-box;
        display: grid;
        grid-template-columns: repeat(1, 189px);
        grid-template-rows: 40px;
        grid-gap: 5px;
        overflow-y: scroll;
        &::-webkit-scrollbar {
          width: 0;
        }
        .spawned-prop {
          background-color: $color-1;
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 3px;
          box-sizing: border-box;
          height: 40px;
          &.selected {
            padding: 1px;
            border: 2px solid $color-2;
          }
        }
      }
      .manage-objects {
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        box-sizing: border-box;
        border-bottom: 2px solid $color-2;
        div {
          background-color: $color-2;
          box-sizing: border-box;
          padding: 5px 8px;
          width: 100px;
          height: 30px;
          &:active {
            background-color: orangered;
          }
        }
      }
      .main {
        height: 430px;
        box-sizing: border-box;
        display: flex;
        .left, .right {
          color: aliceblue;
          width: 199px;
          height: 430px;
          .title {
            box-sizing: border-box;
            padding: 5px;
          }
          &.left {
            border-right: 1px solid $color-2;
          }
          &.right {
            border-left: 1px solid $color-2;
            .prop-state-wrapper {
              .prop-state{
                box-sizing: border-box;
                padding: 5px;
                .prop-state-title-wrapper {
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                  padding: 5px;
                }
                .prop-state-input {
                  input {
                    opacity: 0.8;
                    outline: none;
                    width: 170px;
                    -webkit-appearance: none;  /* Override default CSS styles */
                    border-radius: 2px;
                    background-color: $color-2;
                    &::-webkit-slider-thumb {
                      -webkit-appearance: none; /* Override default look */
                      appearance: none;
                      width: 10px;
                      height: 10px;
                      background: $color-1; /* Green background */
                      cursor: pointer; /* Cursor on hover */
                    }
                    &:hover {
                      opacity: 1;
                    }
                  }
                }
              }
            }
          }
        }
      }
      .anim-wrapper {
        border-top: 2px solid $color-2;
        color: aliceblue;
        .anim-title {
          height: 20px;
        }
        .content {
          height: 60px;
          box-sizing: border-box;
          padding: 3px 5px 5px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .dict, .anim {
            width: 150px;
            height: 50px;
            input {
              outline: none;
              border: none;
            }
          }
          .buttons {
            display: flex;
            justify-content: space-between;
            width: 70px;
            .svg {
              height: 35px;
              width: 35px;
              color: aliceblue;
              svg {
                height: 35px;
                width: 35px;
              }
            }
          }
        }
      }
    }
  }
}

.prompt {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  width: 250px;
  height: 185px;
  border: 2px solid $color-2;
  background-color: $color-1;
  color: aliceblue;
  padding: 5px;
  box-sizing: border-box;
  .title-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 20px;
    .svg {
      height: 20px;
      width: 20px;
      &:hover {
        color : $color-2;
      }
      svg {
        height: 100%;
        width: 100%;
      }
    }
  }
  .select {
    padding: 5px;
    box-sizing: border-box;
    margin-top: 5px;
    #bones {
      width: 100%;
      box-sizing: border-box;
      outline: none;
      height: 25px;
    }
  }
  .input {
    box-sizing: border-box;
    padding: 5px;
    input {
      margin-top: 5px;
      width: 100%;
      box-sizing: border-box;
      outline: none;
      height: 25px;
    }
  }
  .button {
    margin-top: 20px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .ok {
      box-sizing: border-box;
      padding-left: 15px;
      padding-right: 15px;
      background-color: $color-2;
      color: black;
      &:active {
        background-color: orangered;
      }
    }
  }
}

.alert-wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 300px;
  height: 120px;
  background-color: $color-1;
  transform: translate(-50%,-50%);
  border: 2px solid $color-2;
  box-sizing: border-box;
  header {
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 5px;
    .title-wrapper {
      display: flex;
      .title {
        background-color: $color-2;
        height: 20px;
        padding-left: 5px;
        padding-right: 5px;
      }
      .top-left-corner {
        width: 20px;
        height: 20px;
        background: linear-gradient(135deg, $color-2 50%, rgba(0,0,0,0) 50%);
      }
    }
    .exit {
      height: 15px;
      width: 15px;
      display: flex;
      align-items: center;
      padding-right: 5px;
      padding-top: 5px;
      color: aliceblue;
      &:hover {
        color: $color-2;
      }
      svg {
        height: 100%;
        width: 100%;
      }
    }
  }
  textarea {
    margin-top: 5px;
    box-sizing: border-box;
    resize: none;
    height: 80px;
    width: 285px;
    outline: none;
    border: none;
    color : aliceblue;
    background-color: black;
    padding: 5px;
    overflow-y: scroll;
  }
}